h1{
    font-family: 'Times New Roman', Times, serif;
    font-style: italic;
    font-weight: 500;
    font-size: 3rem;
}
p {
    font-style: italic;
}
.footer{
    margin: 0%;
    padding: 0%;
    background-color: #4e3c5836;
    text-align: center;
    position: relative;
    bottom: 0px;
}
.footer p{
    padding: 2rem;
    font-size: 1rem;
    letter-spacing: 5px;
}
.footer span {
    transition: all .5s ease-in-out;
}
.footer span:hover{
    letter-spacing: 10px;
}
